<template>
  <div class="row mt-3">
    <div class="col-md-4 mx-auto offset-md-3">
      <div class="card">
        <div class="card-header bg-dark text-white text-center">
          Editar estudiante
        </div>
        <div class="card-body mx-auto">
          <form v-on:submit="actualizar($event)">
            <div class="d-grid col-6 mx-auto mb-3">
              <img v-if="this.foto" height="100" :src="this.foto" id="fotoimg" class="img-thumbnail" alt="">
              <img v-else height="100" src="https://cdn3.iconfinder.com/data/icons/avatar-user/64/Artboard_34-256.png" class="img-thumbnail" id="fotoimg" alt="">
            </div>
            <div class="input-group mb-3">
              <span class="input-group-text"><i class="fa-solid fa-user"></i></span>
              <input type="text" v-model="nombre" id="nombre" placeholder="Ingrese su nombre" required maxlength="50" class="form-control">
            </div>
            <div class="input-group mb-3">
              <span class="input-group-text"><i class="fa-solid fa-user"></i></span>
              <input type="text" v-model="apellido" id="apellido" placeholder="Ingrese su apellido" required maxlength="50" class="form-control">
            </div>
            <div class="input-group mb-3">
              <span class="input-group-text"><i class="fa-solid fa-calendar"></i></span>
              <input type="date" v-model="estudiante_fecnac" id="estudiante_fecnac" class="form-control">
            </div>
            <div class="input-group mb-3">
              <span class="input-group-text"><i class="fa-solid fa-address-card"></i></span>
              <input type="text" v-model="estudiante_dni" id="estudiante_dni" placeholder="Ingrese su DNI" required maxlength="10" class="form-control">
            </div>
            <div class="input-group mb-3">
              <span class="input-group-text"><i class="fa-solid fa-phone"></i></span>
              <input type="tel" v-model="estudiante_telf" id="estudiante_telf" placeholder="Ingrese su teléfono" required maxlength="15" class="form-control">
            </div>
            <div class="input-group mb-3">
              <span class="input-group-text"><i class="fa-solid fa-envelope"></i></span>
              <input type="email" v-model="email" id="email" placeholder="Ingrese su correo electrónico" required maxlength="100" class="form-control">
            </div>
            <div class="input-group mb-3">
              <span class="input-group-text"><i class="fa-solid fa-gift"></i></span>
              <input v-on:change="previsualizarFoto" type="file" accept="image/png, image/jpeg, image/jpg, image/gif" class="form-control">
            </div>
            <div class="d-grid col-8 mx-auto mb-3">
              <button class="btn btn-warning"><i class="fa-solid fa-refresh"></i> Guardar cambios</button>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mostrarAlerta, enviarSolicitud } from '../funciones';
import { useRoute } from 'vue-router';
import axios from 'axios';

export default {
  data() {
    return {
      id: 0,
      nombre: '',
      apellido: '',
      estudiante_fecnac: '',
      estudiante_dni: '',
      estudiante_telf: '',
      email: '',
      foto: '',
      url: 'http://34.125.126.86/api/estudiantes',
      cargando: false
    };
  },
  mounted() {
    const route = useRoute();
    this.id = route.params.id;
    this.url += '/' + this.id;
    this.getEstudiante();
  },
  methods: {
    getEstudiante() {
      axios.get(this.url)
        .then(res => {
          const data = res.data.data;
          this.nombre = data.nombre;
          this.apellido = data.apellido;
          this.estudiante_fecnac = data.estudiante_fecnac;
          this.estudiante_dni = data.estudiante_dni;
          this.estudiante_telf = data.estudiante_telf;
          this.email = data.email;
          this.foto = data.foto;
        })
        .catch(error => {
          console.error('Error al obtener datos del estudiante:', error);
          mostrarAlerta('Error al obtener datos del estudiante', 'error');
        });
    },
    actualizar(event) {
      event.preventDefault();
      var miFoto = document.getElementById('fotoimg');
      this.foto = miFoto.src;
      
      if (this.nombre.trim() === '') {
        mostrarAlerta('Ingrese un nombre', 'warning', 'nombre');
      } else if (this.apellido.trim() === '') {
        mostrarAlerta('Ingrese un apellido', 'warning', 'apellido');
      } else {
        var parametros = {
          nombre: this.nombre.trim(),
          apellido: this.apellido.trim(),
          estudiante_fecnac: this.estudiante_fecnac,
          estudiante_dni: this.estudiante_dni.trim(),
          estudiante_telf: this.estudiante_telf.trim(),
          email: this.email.trim(),
          foto: this.foto.trim()
        };
        enviarSolicitud('PUT', parametros, this.url, 'Estudiante actualizado!!');
      }
    },
    previsualizarFoto(event) {
      var reader = new FileReader();
      reader.readAsDataURL(event.target.files[0]);
      
      reader.onload = () => {
        var miFoto = document.getElementById('fotoimg');
        miFoto.src = reader.result;
        this.foto = miFoto.src;
      };
    }
  }
}
</script>
